import "./ProductsBlock.style.css";

import Input from "../Input/Input";
import Item from "../Item/Item";

import { useMemo, useState } from "react";
import { items as itemsList } from "../../static_data/items";

const ProductsBlock = () => {
    const [inputValue, setInputValue] = useState<string>("");
    const [items, setItems] = useState([...itemsList]);
    const [color, setColor] = useState<string>("");
    const [size, setSize] = useState<string>("");
    const [brand, setBrand] = useState<string>("");

    const sortedItems = useMemo(() => {
        return items.filter(i => i.title.includes(inputValue))
                    .filter(i => {
                        if (!color) {
                            return i;
                        }
                        else {
                            return i.colors.includes(color)
                        }
                    })
                    .filter(i => {
                        if (!size) {
                            return i;
                        }
                        else {
                            return i.sizes.includes(parseInt(size));
                        };
                    })
                    .filter(i => {
                        if (!brand) {
                            return i;
                        }
                        else {
                            return i.brands.includes(brand);
                        }
                    })
    }, [items, inputValue, color, brand, size]);

    return (
        <div>
            <Input 
                onInputChange={setInputValue} 
                onBrandChange={setBrand}
                onColorChange={setColor}
                onSizeChange={setSize}
                placeholder="Пошук товару"
            />

            <div className="products_block">
               {
               sortedItems.length ? 
                sortedItems.map(i => {
                    return (
                        <Item imgURL={i.imgURL} title={i.title} colors={i.colors} price={i.price} key={i.price} sizes={i.sizes} brands={i.brands}/>
                    )
                })
               : <div className="nothing_wrapper">Нічого не знайдено :(</div>
            }
               
            </div>
        </div>
    );
};

export default ProductsBlock;